.topBar {
  overflow: hidden;
  position: fixed;
  width: 100%;
  height: 50px;
  background-color: #eee;
  z-index: 999;
}

.w {
  width: 860px;
  margin: auto;
}
.logo {
  float: left;
  font-size: 25px;
  color: aliceblue;
  line-height: 50px;
  font-family: "Times New Roman", Times, serif;
  font-weight: 600;
  color: #1890ff;
}
.loginBtn {
  float: right;
  margin-top: 8px;
  margin-left: 20px;
}
.body {
  margin-top: 50px;
}
.tudolist {
  margin: 0;
  padding: 0;
}
li {
  margin: 0;
  list-style: none;
}
.items {
  position: relative;
  height: 40px;
  line-height: 40px;
  margin-top: 10px;
  font-size: 20px;
  background-color: #eee;
  border-radius: 10px;
}
.checkbox {
  position: absolute;
  top: 7px;
  left: 10px;
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.itemsName {
  overflow: hidden;
  display: inline-block;
  margin-left: 55px;
  width: 750px;
  height: 40px;
}
.closeBtn {
  position: absolute;
  right: 20px;
  top: 10px;
  cursor: pointer;
}
